import React from 'react'
import posts from './posts'
import Card from './Card'
import { db, storage } from './config'
import PublishIcon from '@material-ui/icons/Publish';
import { useEffect, useState } from 'react'

const Feed = () => {

    const [tasvir,setTasvir] = useState([])

    useEffect(
        () => {
            db.collection('imagesOfBennyApp').onSnapshot(snp=>{
                
                setTasvir(snp.docs.map((doc)=>(doc.data())))
            })
        }
    ,[])

    const handleOnchange = (e) => {
        e.preventDefault()
        console.log(e.target.files[0])
        const image = e.target.files[0]
        storage.ref(`images/${image.name}`).put(image)

            .on('state_changed', (snap) => { },
                err => { console.log(err) },
                async () => {
                    const url = await storage.ref(`images/${image.name}`).getDownloadURL()
                    db.collection('imagesOfBennyApp').add(
                        {
                            size: image.size,
                            url
                        }
                    )
                })
    }

    return (
        <div className='feed'>
            <div className='form'>
                <form>
                    <label>
                        <PublishIcon />
                        <input className='input_file' type='file'
                            onChange={handleOnchange} />
                    </label>
                  name:  <input className='input_name' />
                </form>
            </div>

            {tasvir.map(post => (<Card key={post.size} image={post.url} />))}
        </div>
    )
}

export default Feed
